.btnSign, 
.btnLog {
    padding: 5px 8px;
    font-size: 17px;
}

.btnSign:link,
.btnSign:visited, 
.btnLog:link,
.btnLog:visited {
    padding: 5px 20px;
    font-size: 17px;
    text-decoration: none;
    color: #f7f7f7;
    border: 1px solid #d4d4d4;
    border-radius: 15px;
    transition: all .3s;
}

.btnSign:link,
.btnSign:visited {
    background-color: #ff7b00;
    margin-right: 20px;
}

.btnSign:hover,
.btnSign:active, 
.btnLog:hover,
.btnLog:active {
    background-color: #ff912a;
}