.Navigation {
    background-color: #17007c;
    padding: 5px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Navigation ul {
    list-style: none;
    display: flex;
    align-items: center;
}

.Navigation ul li {
    margin-right: 30px;
}

.Navigation ul li a:link,
.Navigation ul li a:visited {
    text-decoration: none;
    color: #f1f1f1;
    font-size: 18px;
    transition: all .3s;
}

.Navigation ul li a:active,
.Navigation ul li a:hover {
    color: #ff912a;
    font-size: 18px;
}