.loginPage {
    height: 100vh;
    color: #ffffff;
}

.form {
    width: 25rem;
    background-color: #000000b6;
    padding: 4rem 5rem;
    position: absolute;
    border-radius: 5px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.title {
    font-size: 1.9rem;
    margin: 0 0 1.5rem 0;
    text-align: center;

}

.input {
    font-family: inherit;
    font-size: 1.5rem;
    padding: 0.5rem 1rem;
    color: #838383;
    border-radius: 4px;
    background-color: rgba(fff, .5);
    border: none;
    border-bottom: 3px solid transparent;
    width: 80%;
    display: block;
    transition: all .3s;
}

    /* @include respond(tab-port) {
        width: 100%;
    } */

.input::-webkit-input-placeholder {
    color: #838383;
}

.input:focus {
    outline: none;
    box-shadow: 0 1rem 2rem rgba(#fff, .1);
    border-bottom: 3px solid #65be11;
}

.input:focus:invalid {
    border-bottom: 3px solid #c00101;
}
    


.label {
    font-size: 1.2rem;
    font-weight: 700;
    margin-left: 1rem;
    margin-top: .7rem;
    margin-bottom: .7rem;
    display: block;
    transition: all .3s;
}

.input:placeholder-shown + .label {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-4rem);
}

.logBtn {
    background-color: #e6be0f;
    border: 1px solid #bdbdbd;
    color: #fff;
    padding: .5rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 5px;
    margin: 1rem 0 0 0;
    text-align: center;
}

.backImage {
    width: 100%;
    height: 100%;
    object-position: center;
}